.button {
    cursor: pointer;

    height: 64px;
    width: 100%;
    max-width: 350px;

    border-radius: 4px;

    font-family: "IBM Plex Mono";
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;

    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
/* 
    border: 1px solid var(--color-secondary);
    box-shadow: 0px 8px 30px rgba(63, 142, 0, 0.5); */

    background-color: var(--color-primary);
    color: var(--color-font-secondary);
}
